<template>
  <div v-loading="loading">
    <div class="nav">
      <el-button type="text" icon="el-icon-arrow-left" class="back" @click="$router.back()">
        {{$t('order-meeting.statistics.undo')}}
      </el-button>
      <span style="padding-left: 10px;color:#999">
        {{$t('order-meeting.statistics.comment_detail.data_analysis')}} /
      </span>
        {{$t('order-meeting.statistics.comment_detail.comment_data')}}
    </div>
    <div class="table">
      <el-table
        :data="paginationOrderList">
        <el-table-column
          prop="content"
          :label="shoeName">
        </el-table-column>
        <!-- <el-table-column
          :label="commentDetail.length + ''"
          width="100px">
        </el-table-column> -->
      </el-table>
    </div>
    <el-pagination
      v-if="commentDetail.length > 10"
      layout="prev, pager, next"
      class="pagination"
      :page-size="10"
      @current-change="(e)=>{currentPage = e} "
      small
      :total="commentDetail.length">
    </el-pagination>
  </div>
</template>

<script>
import Vue from 'vue';
import { getCommentDetails } from '@/api/order-meeting';

export default Vue.extend({
  name: 'ConfirmIntention',
  props: {
    colorId: {
      default() {
        return this.$route.query.colorId;
      },
    },
    shoeName: {
      default() {
        return this.$route.query.shoeName;
      },
    },
    workspaceId: {
      default() {
        return this.$route.query.workspaceId;
      },
    },
  },
  data() {
    return {
      currentPage: 1,
      commentDetail: [],
      loading: false,
    };
  },
  created() {
    this.getCommentDetails();
  },
  methods: {
    async getCommentDetails() {
      this.loading = true;
      const { colorId } = this;
      this.commentDetail = (await getCommentDetails(colorId, this.workspaceId)).data;
      this.loading = false;
    },
  },
  computed: {
    paginationOrderList() {
      return this.commentDetail.slice(
        this.currentPage * 10 - 10, this.currentPage * 10,
      );
    },
  },
});
</script>

